<template>
  <div class="container">
    <div class="list__top">
      <wux-row>
        <wux-col span="10">
          <view class="placeholder">
            <wux-search-bar clear maxlength="8"/>
          </view>
        </wux-col>
        <wux-col span="2">
          <view class="tianjia" @click="scanCode">
            <wux-icon type="ios-add" color="#ffffff" size="20"/>
          </view>
        </wux-col>
      </wux-row>
    </div>

    <view class="page__list" v-if="list">
      <van-swipe-cell right-width="120" v-for="(item,index) in list" :key="index">
        <view class="li" @click="ToList">
          <div class="img">
            <img src="/static/img/wd.jpg">
          </div>
          <div class="name">{{item.name}}</div>
          <div class="txt">
            <span class="txt-li">温度:{{item.temp}}</span>
            <span class="txt-li">湿度:{{item.humidity}}</span>
          </div>
        </view>
        <view slot="right">
          <div class="right-box">
            <div class="edit" @click="ToEdit(item.id)">修改</div>
            <div class="del" @click="ToDelete(item.id,index)">删除</div>
          </div>
        </view>
      </van-swipe-cell>
    </view>
    <wux-dialog id="wux-dialog"/>
    <wux-toast id="wux-toast"/>
  </div>
</template>

<script>
import { $wuxDialog, $wuxToast } from "../../wux/index";
export default {
  data() {
    return {
      list: []
    };
  },
  methods: {
    GetData() {
      this.request("device/getDeviceList")
        .then(res => {
          this.list = res.content;
          console.log(this.list);
        })
        .catch(e => {});
    },
    ToDelete(id, index) {
      let _this = this;
      $wuxDialog().open({
        resetOnClose: true,
        title: "确定删除该设备？",
        buttons: [
          {
            text: "取消"
          },
          {
            text: "确定",
            type: "primary",
            onTap(e) {
              _this
                .request("device/deleteDevice", { id: id }, "POST")
                .then(res => {
                  $wuxToast().show({
                    type: "text",
                    duration: 1500,
                    color: "#fff",
                    text: "删除成功"
                  });
                  _this.list.splice(index, 1);
                })
                .catch(e => {});
            }
          }
        ]
      });
    },
    ToEdit(id) {
      this.Push("/pages/device/index", {
        id: id,
        edit: true
      });
    },
    ToList() {
      this.$router.push({ path: "/pages/detail/index" });
      //thit.Push("/pages/list/index");
    },
    scanCode() {
      let _thit = this;
      wx.scanCode({
        success(res) {
          if (res.result) {
            let str = res.result.split("	");
            if (str.length == 1) {
              let str2 = res.result.split("\n");
              _thit.Push("/pages/device/index", {
                appKey: str2[0],
                devEui: str2[1]
              });
            } else {
              _thit.Push("/pages/device/index", {
                appKey: str[0],
                devEui: str[1]
              });
            }
          }
        },
        fail(err) {
          $wuxDialog().alert({
            resetOnClose: true,
            title: "提示",
            content: "扫码失败，请重新扫码!"
          });
        }
      });
    }
  },
  created() {
    //this.GetData();
  }
};
</script>

<style scoped>
.list__top {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  height: 44px;
  z-index: 10;
}

.tianjia {
  display: block;
  height: 34px;
  margin: 6px 10px;
  background: #0c60ee;
  border-radius: 6px;
  position: relative;
}

.tianjia ._wux-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.page__list {
  overflow: hidden;
  padding: 44px 0px;
}

.right-box {
  width: 120px;
}

.page__list .edit,
.page__list .del {
  float: left;
  display: block;
  width: 50%;
  font-size: 16px;
  line-height: 92px;
  text-align: center;
  color: #ffffff;
}

.page__list .edit {
  background: #cccccc;
}

.page__list .del {
  background: red;
}

.page__list .li {
  background: #ffffff;
  display: block;
  overflow: hidden;
  padding: 10px 15px;
  border-bottom: 1px solid #f3eeee;
}

.page__list .img {
  width: 70px;
  height: 70px;
  float: left;
  margin-right: 10px;
}

.page__list .img img {
  width: 100%;
  height: 100%;
}

.page__list .name {
  font-size: 16px;
  line-height: 28px;
  font-weight: bold;
  margin-top: 8px;
  margin-bottom: 6px;
}

.page__list .txt {
  margin-bottom: 8px;
  font-size: 14px;
}

.page__list .txt .txt-li {
  margin-right: 10px;
}
</style>



